import React, { useState, useEffect } from 'react';
import {
  Text,
  View,
  StyleSheet,
  TouchableOpacity,
  TextInput,
  ScrollView,
  CameraRoll,
  Image,
} from 'react-native';
import { BarCodeScanner } from 'expo-barcode-scanner';
import { ToastAndroid } from 'react-native';
var RNFS = require('react-native').NativeModules.RNFS;
import * as WebBrowser from 'expo-web-browser';
import * as Sharing from 'expo-sharing';
import QRCode from 'react-native-qrcode-svg';
import colors from './colors';
import * as FileSystem from 'expo-file-system';
import QRPlainTextCreater from './plainTextQRcode';
import QRPlainTextCreaterCom from './plainTextQRcreaterCom';
import QRWifiCreater from './wifiQRcreater';

var history = [];
var value1;
var text = 'please enter some text';
export default class QRCodeGenerator extends React.Component {
  constructor() {
    super();
    this.state = {
      selectedQrFormat: '',
    };
  }
  render() {
    if (this.state.selectedQrFormat === '') {
      return (
        <View style={styles.container}>
          <ScrollView>
            <View style={styles.subContainer}>
              <TouchableOpacity
                style={[styles.button, { height: 100 }]}
                onPress={() => {
                  this.setState({ selectedQrFormat: 'wifi' });
                }}>
                <Image
                  source={require('./wifi1.png')}
                  style={{ width: 85, height: 65, alignSelf: 'center' }}
                />
                <Text style={styles.buttonText}>Create QR Code for wifi</Text>
              </TouchableOpacity>

              <TouchableOpacity
                style={[styles.button, { height: 100 }]}
                onPress={() => {
                  this.setState({ selectedQrFormat: 'text' });
                }}>
                <Image
                  source={require('./text.png')}
                  style={{ width: 85, height: 65, alignSelf: 'center' }}
                />
                <Text style={styles.buttonText}>Create QR Code for text</Text>
              </TouchableOpacity>

              <TouchableOpacity
                style={[styles.button, { height: 100 }]}
                onPress={() => {
                  this.setState({ selectedQrFormat: 'contact' });
                }}>
                <Image
                  source={require('./contact.png')}
                  style={{ width: 85, height: 65, alignSelf: 'center' }}
                />
                <Text style={styles.buttonText}>
                  Create QR Code for contact
                </Text>
              </TouchableOpacity>

              <TouchableOpacity
                onPress={() => {
                  this.props.navigation.navigate('M');
                }}
                style={styles.button}>
                <Text style={styles.buttonText}>Back</Text>
              </TouchableOpacity>
            </View>
          </ScrollView>
        </View>
      );
    } else if (this.state.selectedQrFormat === 'wifi') {
      return (
        <View>
          <ScrollView>
            <QRWifiCreater />
            <TouchableOpacity
              onPress={() => {
                this.setState({ selectedQrFormat: '' });
              }}
              style={styles.button}>
              <Text style={styles.buttonText}>Back</Text>
            </TouchableOpacity>
          </ScrollView>
        </View>
      );
    } else if (this.state.selectedQrFormat === 'text') {
      return this.props.navigation.navigate('PlainTextCreater');
    } else if (this.state.selectedQrFormat === 'contact') {
      return this.props.navigation.navigate('ContactsQR');
    }
  }
}
