import React, { useState, useEffect } from 'react';
import {
  Text,
  View,
  StyleSheet,
  TouchableOpacity,
  TextInput,
  ScrollView,
  CameraRoll,
  Image,
  Platform,
} from 'react-native';
import { BarCodeScanner } from 'expo-barcode-scanner';
import { ToastAndroid } from 'react-native';
var RNFS = require('react-native').NativeModules.RNFS;
import * as WebBrowser from 'expo-web-browser';
import * as Sharing from 'expo-sharing';
import QRCode from 'qrcode';
import colors from './colors';
import * as FileSystem from 'expo-file-system';
var history = [];
var value1;
var text = 'please enter some text';
var supportedValue = '';
export default class QRWifiCreater extends React.Component {
  constructor() {
    super();
    this.state = {
      usersvalue: 'please enter some text',
      userscodestyle: 'black',
      userssize: 100,
      usersbackgroundcolor: 'white',
      imageURL: '',
      generate: false,
      password: '',
    };
  }
  render() {
    var setText = async (e) => {
      var text = e;
      var addedText = text;
      if (text !== null) {
        if (text !== '' && text !== 'your value for qr code') {
          await this.setState({ usersvalue: addedText });
          var qr = await QRCode.toDataURL(this.state.usersvalue);
          this.setState({ imageURL: qr });
        }
      }
    };
    var download = function (uri, filename, callback) {
      request.head(uri, function (err, res, body) {
        console.log('content-type:', res.headers['content-type']);
        console.log('content-length:', res.headers['content-length']);

        request(uri).pipe(fs.createWriteStream(filename)).on('close', callback);
      });
    };
    var setPassword = async () => {
      var addedText = `Wifi Name: ${this.state.usersvalue} \n  \nPassword:${this.state.password}`;
      if (text !== null) {
        if (text !== '' && text !== 'your value for qr code') {
          await this.setState({ usersvalue: addedText });
          var qr = await QRCode.toDataURL(this.state.usersvalue);
          this.setState({ imageURL: qr });
        }
      }
    };

    var setColor = (e) => {
      var text = e.toLowerCase().trim();
      if (colors.includes(text)) {
        if (text !== '' && text !== 'your value for qr code') {
          this.setState({ userscodestyle: text });
        }
      } else {
        this.setState({ userscodestyle: 'black' });
      }
    };
    var setBgColor = (e, state) => {
      var text = e.toLowerCase().trim();
      if (colors.includes(text)) {
        if (text !== '' && text !== 'your value for qr code') {
          this.setState({ usersbackgroundcolor: text });
        }
      } else {
        this.setState({ usersbackgroundcolor: 'white' });
      }
    };
    var getData = (val) => {
      this.setState({ password: val });
    };

    return (
      <View style={styles.container}>
        <ScrollView>
          <View style={styles.subContainer}>
            <Text style={styles.displayTextS}>Type your Wifi Name</Text>
            <TextInput
              style={styles.input}
              onChange={(e) => {
                this.setState({ generate: false });
                if (Platform.OS !== 'web') {
                  setText(e.nativeEvent.text);
                } else {
                  setText(e.target.value);
                }
              }}
            />

            <Text style={styles.displayTextS}>Type your Wifi Password</Text>
            <TextInput
              style={styles.input}
              onChange={(e) => {
                this.setState({ generate: false });
                if (Platform.OS !== 'web') {
                  getData(e.nativeEvent.text);
                } else {
                  getData(e.target.value);
                }
              }}
            />

            <TouchableOpacity
              style={styles.button}
              onPress={() => {
                setPassword();
                this.setState({ generate: true });
              }}>
              <Text style={styles.buttonText}>Generate</Text>
            </TouchableOpacity>
            <View style={[styles.qrcode, { marginLeft: -2, marginBottom: 80 }]}>
              {this.state.generate === true ? (
                <Image
                  style={{
                    justifyContent: 'center',
                    alignSelf: 'center',
                    marginTop: 50,
                    width: 200,
                    height: 200,
                  }}
                  alt={'change the value so you can see the qr code here'}
                  source={{ uri: this.state.imageURL }}
                />
              ) : null}

              {Platform.OS !== 'web' ? (
                alert("Sorry You can't download QRCode in Mobile")
              ) : (
                <a href={this.state.imageURL.toString()} download>
                  Download QRCode
                </a>
              )}
            </View>
          </View>
        </ScrollView>
      </View>
    );
  }
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: 'cyan',
  },
  button: {
    justifyContent: 'center',
    alignSelf: 'center',
    borderWidth: 3,
    borderRadius: 15,
    backgroudColor: 'red',
    borderColor: 'darkgreen',
    marginTop: 50,
    width: 200,
    height: 50,
  },
  button1: {
    justifyContent: 'center',
    alignSelf: 'center',
    borderWidth: 3,
    borderRadius: 15,
    backgroudColor: 'red',
    borderColor: 'darkgreen',
    marginTop: 600,
    width: 200,
    height: 50,
  },
  subContainer: {
    marginTop: 50,
    flex: 1,
    borderWidth: 2,
    alignItems: 'center',
    backgroundColor: 'cyan',
  },
  qrcode: {
    marginTop: 15,
  },
  input: {
    borderColor: 'black',
    borderWidth: 2,
    width: 250,
    height: 25,
    textAlign: 'center',
    marginTop: 30,
    alignSelf: 'center',
  },
  buttonText: {
    textAlign: 'center',
    color: 'darkblue',
  },
  displayTextS: {
    color: 'black',
    fontWeight: 'bolder',
    fontSize: 20,
    marginBottom: 10,
    textAlign: 'center',
  },
  input: {
    backgroundColor: 'white',
    borderColor: 'blue',
    borderWidth: 2,
    width: 200,
    textAlign: 'center',
    marginTop: 50,
    alignSelf: 'center',
  },
  title: {
    margin: 10,
    textAlign: 'center',
    fontSize: 20,
    fontWeight: 'bold',
    textAlign: 'center',
    marginBottom: 15,
  },
});
